.card-container {
  cursor: pointer;
  height: 400px;
  width: 400px;
  -webkit-perspective: 400px;
          perspective: 400px;
  position: relative;
  margin: auto;
  -webkit-transition: ease-in;
  -o-transition: ease-in;
  transition: ease-in;
}

.card {
  height: 400px;
  width: 400px;
  position: absolute;
  border: 1px solid #300;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.card:hover {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  -webkit-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.card .front {
  height: 400px;
  width: 400px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  overflow: hidden;
  text-align: center;
  background-color: rgba(255, 251, 251, 1);
}

.card .back {
  height: 400px;
  width: 400px;
  text-align: center;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.eng{
  color: #333;
  font-size: 2.3em;
  padding-top: 40%;
}

.han{
  color: #333;
  font-size: 6.3em;
  padding-top: 20%;
}

.pin{
    color: rgba(200, 10, 10, 1);
    font-size: 2.3em;
    padding-top: 10%;
}


